import { useEffect } from "react";
import {
  ActionRedirect,
  identify,
  Mes1,
  Mes2,
  Mes3,
  Mes4,
  Mes5,
  Mes6,
  Mes7,
  Mes8,
} from "../func";
const { messages } = window.txt;

const Head = () => {
  return (
    <header className="w-100 h-10 p-20 flx flx-col flx-jc-ce flx-ai-ce bg-img shdw-bot">
      <h4 className="lato txt-wht txt-al-ce">Customer Service</h4>
      <div className="flx w-100 flx-jc-ce flx-ai-ce m-t-2">
        <p className="mont p-20 mont-b txt-wht txt-al-l">HOME</p>
        <p className="mont p-20 mont-b txt-wht m-l-auto txt-al-r">MANAGE</p>
      </div>
    </header>
  );
};

const Messages = () => {
  return (
    <div className="flx flx-jc-ce flx-ai-ce m-t-2 flx-shr">
      <div className="flx flx-col flx-jc-strt flx-ai-ce h-100">
        <img src="./img/user.png" alt="user" />
        <p className="lato">Julia</p>
      </div>
    </div>
  );
};

const Chat = (props) => {
  const { mes, set } = props;
  const { mes1, mes2, mes3, mes4, mes5, mes6, mes7, mes8 } = mes;

  return (
    <section className="h-90 p-20 ovr-scr chatCont">
      {mes1 && <Mes1 set={set} mess={mes} />}
      {mes2 && <Mes2 set={set} mess={mes} />}
      {mes3 && <Mes3 set={set} mess={mes} />}
      {mes4 && <Mes4 set={set} mess={mes} />}
      {mes5 && <Mes5 set={set} mess={mes} />}
      {mes6 && <Mes6 set={set} mess={mes} />}
      {mes7 && <Mes7 set={set} mess={mes} />}
      {mes8 && <Mes8 set={set} mess={mes} />}
    </section>
  );
};

export { Head, Chat };
