import { useState, useEffect } from "react";
import { Head, Chat } from "./components/Components";

const checkMin = (time) => {
  if (time < 10) {
    return "0" + time;
  }
  return time;
};

const date = new Date();
const time = date.getHours() + " : " + checkMin(date.getMinutes());

function App() {
  const [mes, setMes] = useState({
    mes1: false,
    mes2: false,
    mes3: false,
    mes4: false,
    mes5: false,
    mes6: false,
    mes7: false,
    mes8: false,
  });

  useEffect(() => {
    setTimeout(() => {
      setMes({
        ...mes,
        mes1: true,
      });
    }, 1500);
  }, []);

  // useEffect(() => {
  //   let cont = document.querySelector(".chatCont");
  //   let height = cont.scrollHeight;

  //   cont.scrollTop = height;
  // }, [mes]);

  return (
    <div className="flx flx-col flx-jc-ce flx-ai-ce w-100 h-100">
      <Head />

      <small className="lato m-t-2">{time}</small>
      <Chat mes={mes} set={setMes} />
    </div>
  );
}

export default App;
